@import "../partials/variables";

//Theme 09
#atw_style9{
    .atw__title9{
        padding: 20px 0;
        border-top: 1px solid $borderColor;
        border-bottom: 1px solid $borderColor;
        text-align: left;
        &::before{
            content: none;
        }
    }
    .owl-stage-outer{
        padding: 15px 0;
    }
    .atw--single_item{
        transition: $transition;
        &:hover{
            box-shadow: 0 10px 10px rgba(84,77,124,0.1);
            .atw_item_top{
                .atw_item_view{
                    visibility: visible;
                    opacity: 1;
                    bottom: 0;
                }
            }
        }
        .atw_item_top{
            position: relative;
            overflow: hidden;
            .badge--left{
                top: 20px;
                left: 20px;
            }
            .badge--right{
                top: 20px;
                right: 20px;
            }
            .atw_item_view{
                text-align: center;
                padding: 0 20px;
                position: absolute;
                width: 100%;
                left: 0;
                bottom: -15px;
                opacity: 0;
                visibility: hidden;
                transition: $transition;
                a{
                    line-height: 32px;
                    display: block;
                    font-size: 14px;
                    background: #363940;
                    color: #fff;
                }
            }
        }
        .atw_item_bottom{
            text-align: center;
            .atw_item_header{
                margin-bottom: 10px;
            }
            .atw_item_info{
                margin-bottom: 15px;
                .atw_price{
                    color: $theme2;
                    display: block;
                    margin-bottom: 12px;
                    font-size: 18px;
                    del{
                        color: #363940;
                        font-weight: 400;
                        font-size: 16px;
                        display: inline-block;
                        padding-right: 4px;
                    }
                }
            }
            .atw_product_actions2{
                padding-bottom: 25px;
                li{
                    &:first-child, &:last-child{
                        a{
                            border-radius: 0;
                        }
                    }
                }
            }
        }
    }
    .atw_slider_controls{
        .slider--control9{
            position: absolute;
            right: 0;
            margin: 0 4px 0 4px;
            top: 15px;
            transform: none;
            border-radius: 3px;
            transition: 0.3s ease;
            border: 1px solid $borderColor;
            &:hover{
                border-color: $theme2;
                color: #fff;
                background: $theme2;
            }
            &.icon-arrow-left{
                left: auto;
                right: 60px;
            }
            &.icon-arrow-right{
                right: 11px;
            }
        }
    }
    .owl-dots{
        text-align: center;
        width: 100%;
        margin-top: 15px;
        .owl-dot{
            display: inline-block;
            width: 10px;
            height: 10px;
            border-radius: 50%;
            background: transparentize($theme2, 0.8);
            transition: $transition;
            margin-right: 10px;
            &:last-child{
                margin-right: 0;
            }
            &.active{
                background: $theme2;
            }
        }
    }
}